import * as actionTypes from "../actionTypes/results";

export const clearSearchResults = () => ({
  type: actionTypes.CLEAR_SEARCH_RESULTS
});

export const setSearchResults = array => ({
  type: actionTypes.SET_SEARCH_RESULTS,
  array
});

export const clearSelectedSong = () => ({
  type: actionTypes.CLEAR_SELECTED_SONG
});

export const setSelectedSong = obj => ({
  type: actionTypes.SET_SELECTED_SONG,
  obj
});

export const setSongRequests = array => ({
  type: actionTypes.SET_SONG_REQUESTS,
  array
});

export const setCompletedSongRequests = array => ({
  type: actionTypes.SET_COMPLETED_SONG_REQUESTS,
  array
});

export const setTickerString = string => ({
  type: actionTypes.SET_TICKER_STRING,
  string
});

export const setSuccessfulRequestDetails = obj => ({
  type: actionTypes.SET_SUCCESSFUL_REQUEST_DETAILS,
  obj
});
