/* eslint-disable react-hooks/exhaustive-deps */
import React from "react";
import { withRouter } from "react-router-dom";
import {
  makeStyles,
  Typography,
  TextField,
  InputAdornment,
  IconButton,
  List,
  CircularProgress
} from "@material-ui/core";
import { ToggleButton, ToggleButtonGroup } from "@material-ui/lab";
import { Clear, Search, Error, FilterList } from "@material-ui/icons";
import { TOOLBAR_HEIGHT } from "../../constants/index";

import KaraokeSongSummaryListItem from "../../components/KaraokeSongSummaryListItem/KaraokeSongSummaryListItemConnector";

const useStyles = makeStyles(theme => ({
  results: {
    minHeight: `calc(100vh - ${TOOLBAR_HEIGHT}px)`
  },
  headerContainer: {
    position: "sticky",
    top: TOOLBAR_HEIGHT,
    width: "100%",
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3),
    backgroundColor: theme.palette.background.paper,
    zIndex: 100,
  },
  titleContainer: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "baseline"
  },
  hint: {
    display: "flex",
    alignItems: "center"
  },
  filterContainer: {
    width: "100%",
    display: "flex",
    justifyContent: "center",
    flexWrap: "wrap",
    marginBottom: theme.spacing(1)
  },
  textField: {
    maxWidth: "50vw",
    marginRight: theme.spacing(3)
  },
  label: {
    display: "flex",
    alignItems: "center"
  },
  resultsList: {
    display: "flex",
    flexDirection: "column",
    width: "100%",
    height: "100%",
    minHeight: "100%",
    marginBottom: "20px"
  },
  holding: {
    position: "fixed",
    left: 0,
    top: "40vh",
    width: "100%",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "column"
  },
  list: {
    marginBottom: "20px"
  },
  spinner: {
    marginBottom: theme.spacing(3)
  }
}));

const Results = props => {
  const {
    setSelectedButton,
    searchResults,
    gettingSearchResults,
    clearSearchResults,
    history
  } = props;
  const classes = useStyles();

  const [filter, setFilter] = React.useState("");
  const [filterBy, setFilterBy] = React.useState(0);
  const [filteredResults, setFilteredResults] = React.useState(searchResults);

  const filterResults = () => {
    const userInput = filter.toLowerCase();
    setFilteredResults(
      searchResults.filter(obj => {
        const { Artist, Title } = obj;
        const matchArtist = Artist.toLowerCase().includes(userInput);
        const matchTitle = Title.toLowerCase().includes(userInput);

        if (
          (filterBy === 0 && matchTitle) ||
          (filterBy === 1 && matchArtist) ||
          (filterBy === 2 && (matchArtist || matchTitle))
        ) {
          return obj;
        }

        return null;
      })
    );
  };

  React.useEffect(() => {
    setFilteredResults(searchResults);
  }, [searchResults]);

  React.useEffect(() => {
    setSelectedButton(null);

    return () => {
      clearSearchResults();
    }
  }, []);

  React.useEffect(() => {
    if (filter.length === 0) {
      setFilteredResults(searchResults);
    } else if (filter.length >= 1) {
      filterResults();
    }
  }, [filter, filterBy]);

  React.useEffect(() => {
    let redirect;
    if (!gettingSearchResults && searchResults.length === 0) {
      redirect = setTimeout(() => {
        history.push("/");
      }, [7000]);
    }

    if (searchResults.length > 0) {
      // cancel timeout when result returned after this has started
      clearTimeout(redirect);
    }

    return () => {
      clearTimeout(redirect);
    }
  }, [gettingSearchResults, searchResults]);

  const disableFilter = gettingSearchResults || searchResults.length === 0;

  const handleFilterByOnChange = value => {
    if (value === null) {
      return;
    }

    setFilterBy(value);
  };

  let pageContent;
  if (gettingSearchResults) {
    pageContent = (
      <div className={classes.holding}>
        <CircularProgress className={classes.spinner} size={100} />
        <Typography>Getting search results...</Typography>
      </div>
    );
  } else if (searchResults.length === 0) {
    pageContent = (
      <div className={classes.holding}>
        <Error fontSize="large" />
        <Typography>There are no results matching your search</Typography>
        <Typography>Please try again</Typography>
      </div>
    );
  } else {
    pageContent = (
      <List className={classes.list}>
        {filteredResults.map(obj => (
          <KaraokeSongSummaryListItem key={obj.UID} songObj={obj} />
        ))}
      </List>
    );
  }

  return (
    <div className={classes.results}>
      <div id="header-container" className={classes.headerContainer}>
        <div id="title-container" className={classes.titleContainer}>
          <Typography variant="h4" gutterBottom>
            Search Results
          </Typography>
          <div className={classes.hint}>
            <Search />
            <Typography>Select a song to submit a request</Typography>
          </div>
        </div>
        <div id="search=box" className={classes.filterContainer}>
          <TextField
            id="filter-text-field"
            label="Filter results"
            value={filter}
            onChange={e => setFilter(e.target.value)}
            className={classes.textField}
            variant="outlined"
            fullWidth
            disabled={disableFilter}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <FilterList />
                </InputAdornment>
              ),
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton disabled={!filter} onClick={() => setFilter("")}>
                    <Clear />
                  </IconButton>
                </InputAdornment>
              )
            }}
          />
          <ToggleButtonGroup
            size="large"
            value={filterBy}
            exclusive
            onChange={(event, value) => handleFilterByOnChange(value)}
          >
            <ToggleButton key="title" value={0} disabled={disableFilter}>
              <Typography>Title</Typography>
            </ToggleButton>
            <ToggleButton key="artist" value={1} disabled={disableFilter}>
              <Typography>Artist</Typography>
            </ToggleButton>
            <ToggleButton key="both" value={2} disabled={disableFilter}>
              <Typography>Both</Typography>
            </ToggleButton>
          </ToggleButtonGroup>
        </div>
      </div>
      <div id="results-list" className={classes.resultsList}>
        {pageContent}
      </div>
    </div>
  );
};

export default withRouter(Results);
